html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.parrafo {
  text-align: center;
  margin-top: 5rem;
  font-weight: 400;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  background-image: url(../img/imagenlogin.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  filter: sepia(30%);
  height: 30rem;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 95%;
  padding: 3rem 0;
  margin: 0 auto;
  border-left: 1px transparent black;
}

.contenedor-app-cita .app-cita {
  width: 95%;
  padding: 3rem 0;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-app-cita .app-cita {
    width: 70%;
  }
}

.headercita {
  display: grid;
  position: relative;
}
.headercita .logo {
  height: 15rem;
  width: 15rem;
  position: absolute;
  justify-self: center;
  align-self: center;
  filter: brightness(1.1);
  mix-blend-mode: multiply;
  transition: all 1s ease;
}
@media (min-width: 480px) {
  .headercita .logo {
    height: 20rem;
    width: 20rem;
  }
}
@media (min-width: 768px) {
  .headercita .logo {
    height: 40rem;
    width: 40rem;
  }
}

.imagen-cita {
  background-image: url(../img/bannercitas.jpg);
  background-size: cover;
  background-position: center center;
  filter: blur(5px);
  height: 30rem;
}

.contenedor-app-panel {
  display: block;
  width: 95%;
  margin: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app-panel {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-app-panel .app-panel .botones-panel {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}
.contenedor-app-panel .app-panel .botones-panel .boton {
  width: 20rem;
  height: 15rem;
  transition: all 1s ease;
}
.contenedor-app-panel .app-panel .botones-panel .boton:hover {
  transform: scale(1.2);
}

.contenedor-imagenes-panel .imagenes-panel {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6rem;
  height: 100%;
  margin-top: 5rem;
}

.imagen-panel1 {
  background-image: url(../img/panel1.jpg);
  transform: rotate(15deg);
}

.imagen-panel2 {
  transform: rotate(-15deg);
  background-image: url(../img/panel2.jpg);
}

.imagen-panel3 {
  transform: rotate(15deg);
  background-image: url(../img/panel3.jpg);
}

.imagen-panel1,
.imagen-panel2,
.imagen-panel3 {
  background-size: cover;
  background-position: center center;
  filter: blur(2px);
  height: 15rem;
  width: 20rem;
  border-radius: 5px;
  border: 1px solid #FFFFFF;
  transition: all 1.2s ease;
  color: transparent;
  text-align: end;
}
.imagen-panel1:hover,
.imagen-panel2:hover,
.imagen-panel3:hover {
  transform: rotate(0);
  color: #FFFFFF;
  filter: none;
}
@media (min-width: 768px) {
  .imagen-panel1:hover,
  .imagen-panel2:hover,
  .imagen-panel3:hover {
    transform: scale(1.5) translate(-50px);
    z-index: 2;
  }
}

.contenedor-calendario {
  margin-top: 5rem;
}

.seccion {
  display: none;
}

.mostrar {
  display: block;
}

.citas {
  max-height: 60rem;
  overflow-y: scroll;
  margin-bottom: 3rem;
  scroll-behavior: smooth;
  scrollbar-color: #0da6f3 transparent;
  scrollbar-width: thin;
}
.citas .hora {
  border-bottom: 1px solid #FFFFFF;
  margin-bottom: 2rem;
  font-weight: bold;
}
.citas .fecha {
  font-weight: bold;
}
.citas .citaid {
  text-align: center;
  padding-top: 2rem;
  font-weight: bold;
  margin: 0;
}
.citas span {
  color: #0da6f3;
  font-weight: bold;
}

.detalles-cita {
  visibility: hidden;
  height: 0;
  opacity: 0;
}
.detalles-cita.mostrar {
  visibility: visible;
  height: auto;
  opacity: 1;
  transition: all 1s ease-in-out;
}

.listado-servicios {
  display: block;
  gap: 1rem;
  max-height: 80rem;
  overflow-y: scroll;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scrollbar-color: #0da6f3 transparent;
  scrollbar-width: thin;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios.acciones {
  display: flex;
  justify-content: space-evenly;
}
.listado-servicios .servicios {
  background-color: #FFFFFF;
  display: block;
  gap: 1rem;
  margin: 1rem 0;
  text-align: center;
  padding: 1rem 0;
  border-radius: 5px;
  transition: transform 0.2s;
}
.listado-servicios .servicios:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.listado-servicios .servicios .precioservicio {
  color: #0da6f3;
}
.listado-servicios .servicios p {
  text-transform: uppercase;
  font-weight: bold;
  color: #1a1b15;
  margin: 1rem 0;
}
.listado-servicios .seleccionado {
  background-color: #bfbfbf;
}
.listado-servicios .seleccionado:hover {
  cursor: not-allowed;
}

.contenedor-servicios p {
  color: #0da6f3;
  font-weight: bold;
}
.contenedor-servicios span {
  color: #FFFFFF;
}

.precio {
  border-bottom: 1px solid ghostwhite;
  border-top: 1px solid ghostwhite;
  padding: 1rem;
}
.precio:first-child {
  border-top: none;
}

.contenedor-calendario {
  display: block;
}
@media (min-width: 768px) {
  .contenedor-calendario {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.calendario {
  width: 85%;
  max-height: 40rem;
  margin: 3rem auto 0 auto;
}
@media (min-width: 768px) {
  .calendario {
    margin: 0 auto;
  }
}

.nosotros {
  display: block;
  margin: 5rem 0;
  line-height: 1.5;
  height: auto;
}
@media (min-width: 768px) {
  .nosotros {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
  }
}
.nosotros p {
  font-size: 1.8rem;
  margin: 0;
  padding: 0;
}
.nosotros img {
  width: 100%;
}

.equipo {
  display: flex;
  align-items: center;
  height: auto;
  padding: 2rem 0;
  margin-bottom: 4rem;
  background-color: rgba(0, 0, 0, 0.226);
  border-radius: 10px;
  overflow: hidden;
}
@media (min-width: 768px) {
  .equipo {
    height: auto;
  }
}

.miembros {
  display: flex;
  width: 100%;
  animation: slide 20s infinite alternate ease-in-out;
}
.miembros span {
  font-family: "Dancing Script", cursive;
  font-size: 4rem;
  color: #FFFFFF;
}
.miembros p {
  color: gray;
}

@keyframes slide {
  0% {
    margin-left: 0;
  }
  20% {
    margin-left: 0%;
  }
  25% {
    margin-left: -100%;
  }
  50% {
    margin-left: -100%;
  }
  55% {
    margin-left: -200%;
  }
  70% {
    margin-left: -200%;
  }
  75% {
    margin-left: -300%;
  }
  100% {
    margin-left: -300%;
  }
}
.miembro {
  height: 100%;
  flex: 0 0 100%;
  padding: 2rem;
  display: block;
  text-align: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .miembro {
    display: flex;
  }
}
.miembro img {
  filter: grayscale(100%);
  height: 30rem;
  width: 25rem;
}

.imgEspacio {
  display: block;
  width: 95%;
  margin: 6rem 0;
}
@media (min-width: 768px) {
  .imgEspacio {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
    gap: 2rem;
  }
}
.imgEspacio img {
  width: 100%;
}

.formulario {
  width: 80%;
  margin: 0 auto;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
.campo input {
  padding: 1rem 1rem;
  width: 95%;
  border-radius: 10px;
  border: none;
}

.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 10px;
  margin: 3rem auto;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.botoncerrar {
  margin-bottom: 3rem;
  display: flex;
  justify-content: end;
}

.cerrarsesion,
.boton-eliminar {
  color: #FFFFFF;
  padding: 0.5rem;
  border-radius: 5px;
  font-weight: bold;
  transition: all 0.3s linear;
}
.cerrarsesion:hover,
.boton-eliminar:hover {
  transform: scale(1.1);
  background-color: rgb(73, 73, 73);
}

.boton-eliminar {
  margin: 1rem;
  border: none;
  background-color: transparent;
}

.icon {
  margin: 0 auto;
  transition: all 0.3s linear;
}
.icon:hover {
  cursor: pointer;
}
.icon.girar {
  transform: rotate(180deg);
}

.acciones {
  display: flex;
  margin: 3rem 0;
  align-self: end;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .acciones {
    align-items: center;
  }
}
.acciones a {
  color: #FFFFFF;
  padding: 0.5rem;
  border-radius: 5px;
  font-weight: bold;
  transition: all 0.3s linear;
}
.acciones a:hover {
  transform: scale(1.1);
  background-color: rgb(73, 73, 73);
}

.error {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .error {
    width: auto;
  }
}
.error:hover {
  background-color: #980000;
  cursor: pointer;
}

.exito {
  background-color: #329f00;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .exito {
    width: auto;
  }
}
.exito:hover {
  background-color: #226c00;
  cursor: pointer;
}

.alerta {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .alerta {
    width: auto;
  }
}
.alerta:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.swal2-popup {
  padding: 3%;
  height: 35rem;
  width: 35rem;
}
.swal2-popup h2 {
  font-size: 3rem;
  padding: 0;
}
.swal2-popup span {
  width: 6rem;
  height: 6rem;
}

.navegacion {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
  }
}
.navegacion .tabs {
  background-color: #0da6f3;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  margin-bottom: 2rem;
  width: 100%;
  text-transform: uppercase;
  font-weight: bold;
  transition: all 0.8s;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion .tabs:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  .navegacion .tabs:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }
}
.navegacion .tabs:hover {
  cursor: pointer;
  background-color: #FFFFFF;
  color: #0da6f3;
}
@media (min-width: 768px) {
  .navegacion .tabs:hover {
    width: 250%;
  }
}
.navegacion .seleccionado {
  background-color: #FFFFFF;
  color: #0da6f3;
}

@media (min-width: 768px) {
  .paginacion {
    display: flex;
  }
}
.paginacion .ocultar {
  display: none;
}

.footer-cita {
  display: flex;
  flex-direction: column;
  gap: 6rem;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
  background-color: rgba(0, 0, 0, 0.276);
}
@media (min-width: 768px) {
  .footer-cita {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    justify-items: center;
  }
}

.links ul {
  list-style: none;
  padding: 0;
}
.links ul li {
  margin-bottom: 1rem;
  cursor: pointer;
}

.logo img {
  border-radius: 25%;
  width: 10rem;
  height: 10rem;
}

.iconos {
  display: flex;
  gap: 4rem;
}
.iconos img {
  filter: saturate(0%);
  transition: all 1s ease;
}
.iconos img:hover {
  filter: saturate(150%);
  cursor: pointer;
}